.todo-app {
    max-width: 400px;
    margin: 0 auto;
    text-align: center;
}

.input-container {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

input[type="text"] {
    flex: 1;
    padding: 10px;
    font-size: 16px;
}

button {
    padding: 10px 15px;
    margin-left: 10px;
    font-size: 16px;
    cursor: pointer;
}

ul {
    list-style: none;
    padding: 0;
}

li {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    background: #f4f4f4;
    margin-bottom: 10px;
    border-radius: 5px;
}

li.completed span {
    text-decoration: line-through;
}

li span {
    cursor: pointer;
}
